* {
  font-family: 'Nunito', sans-serif;
}

.main-title {
  display: flex;
  color: #1B1B3A;
  margin-top: 50px;
  margin-bottom: 50px;
  background-color: rgba(255, 252, 255, 0.4);
  padding: 15px;
  border-radius: 10px;
  box-shadow: 3px 3px 3px 3px #36413E;
}

.card-title {
  color: #36413E;
}

.container {
  margin: 0;
  padding: 0;
  background-image: url(./pexels-michael-block-3225517.jpg);
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #1B1B3A;
  min-width: 100%;
}

img { 
  width: 100px;
  height: 130px;
  border-radius: 100%;
  margin-bottom: 10px;
}

.cards-list {
  /* border: 2px solid red; */
  display: flex;
  flex-wrap: wrap;
  min-width: 70%;
  max-width: 1500px;
  min-height: 1900px;
  justify-content: space-around;
  align-content: flex-start;
}

.card-container {
  border: 10px solid #D7BCC8;
  border-radius: 10px;
  width: 320px;
  height: 580px;
  background-color: rgba(255, 252, 255, 0.4);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 50px;
  box-shadow: 3px 3px 10px 3px #36413E;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: none;
  background-color: transparent;
}

form {
  display: flex;
  flex-direction: column;
  background-color: rgba(255, 252, 255, 0.4);
  padding: 18px;
  border-radius: 10px;
  width: 400px;
  box-shadow: 3px 3px 3px 3px #36413E;
}

section {
  /* border: 2px solid red; */
  display: flex;
  flex-wrap: wrap;
  width: 70%;
  min-height: 700px;
  justify-content: space-around;
  align-items: center;
}

textarea {
  resize: none;
}

svg {
  width: 30px;
  margin-left: 8px;
  color: #1B1B3A;
}

.filter {
  padding: 18px;
  border-radius: 10px;
  margin-bottom: 70px;
  width: 550px;
  margin-top: 70px;
}

.filter-inputs {
  display: flex;
  justify-content: space-around;
  align-items: baseline;
}

.game {
  position: absolute;
  z-index: 1;
  width: 500px;
  height: 750px;
  margin-top: -200px;
  background-image: url(./pexels-lukas-1419923.jpg);
  background-size: cover;
  border-radius: 30px;
  box-shadow: 3px 3px 3px 3px black;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.game-buttons button {
  margin: 10px;
}

.filter-play-container {
  background-color: rgba(255, 252, 255, 0.4);
  display: flex;
  flex-wrap: wrap;
  width: 70%;
  max-width: 784px;
  justify-content: space-evenly;
  margin-bottom: 100px;
  border-radius: 10px;
  box-shadow: 3px 3px 10px 3px #36413E;
}

.play-button {
  align-self: center;
}